import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { favorites, setWeatherData } from "../../store/weather";
import classes from "./favorites.module.scss";
import FavoriteItem from "./FavoriteItem";
import { useHistory } from "react-router";

import useGenerateTheme from "../../hooks/use-generateTheme";
import { Button } from "@material-ui/core";

function Favorites() {
  const favCities = useSelector(favorites);
  const history = useHistory();

  const { currentTheme } = useGenerateTheme();

  const dispatch = useDispatch();

  const onFavoriteItemClick = (e, weather) => {
    if (e.target.classList.contains("react-switch-handle")) return;
    dispatch(setWeatherData(weather));

    history.push("/weather");
  };

  const NoFavoriteItems = () => (
    <div className={`${classes.night} ${classes[currentTheme]}`}>
      <div className="section container">
        <h1 className="is-size-1 is-size-4-mobile has-text-weight-bold">
          Your favorite section is a little cold..
        </h1>
        <h2 className="is-size-2 is-size-6-mobile">
          Head back to{" "}
          <Button
            onClick={() => history.push("/weather")}
            variant="contained"
            color="primary">
            Home
          </Button>{" "}
          and add cities to make it <span className="warm-text">warmer</span>!
        </h2>
      </div>
    </div>
  );

  return favCities.length === 0 ? (
    <NoFavoriteItems />
  ) : (
    <section className="section container">
      <div className={classes.root}>
        {favCities.map((weatherData) => (
          <FavoriteItem
            onFavoriteItemClick={onFavoriteItemClick}
            key={weatherData.Key}
            weatherData={weatherData}
          />
        ))}
      </div>
    </section>
  );
}

export default Favorites;
