const generateBgClassFromTemperature = (tempValue) => {
  let className;
  if (tempValue > 20) className = "warm";
  else if (tempValue >= 15) className = "mid";
  else if (tempValue > 10) className = "cold";
  else if (tempValue > 0) className = "ice";

  return className;
};

export default generateBgClassFromTemperature;
