import React, { useState, useEffect } from "react";

import classes from "./TemperatureDisplay.module.scss";
import Switch from "react-switch";
import { primaryDark, primaryLight } from "../../config/config";
import useGenerateTheme from "../../hooks/use-generateTheme";

function TemperatureDisplay({ Temperature }) {
  const { currentTheme } = useGenerateTheme();

  const currentSwitchColor =
    currentTheme === "dark" ? primaryDark : primaryLight;
  const [temState, setTempState] = useState(null);

  useEffect(() => {
    const { Unit, Value } = Temperature;
    setTempState({
      Unit,
      Value,
    });
  }, [Temperature]);

  const switchTemType = (checked, e) => {
    e.stopPropagation();

    setTempState((preTemState) => {
      return preTemState.Unit === "C"
        ? {
            Unit: "F",
            Value: preTemState.Value * 1.8 + 32,
          }
        : {
            Unit: "C",
            Value: (preTemState.Value - 32) / 1.8,
          };
    });
  };
  return (
    temState && (
      <div className={classes.root}>
        <p className="is-size-5">
          {temState.Value.toFixed(2)} <span>&#xb0;</span>
        </p>
        <Switch
          checked={temState.Unit === "C"}
          onChange={(checked, e) => switchTemType(checked, e)}
          onColor={currentSwitchColor}
          offColor={currentSwitchColor}
          handleDiameter={30}
          uncheckedIcon={false}
          checkedIcon={false}
          uncheckedHandleIcon={
            <span style={{ position: "relative", left: "5px", color: "black" }}>
              &#8457;
            </span>
          }
          checkedHandleIcon={
            <span style={{ position: "relative", left: "5px", color: "black" }}>
              {" "}
              &#8451;
            </span>
          }
          boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)"
          activeBoxShadow="0px 0px 1px
        10px rgba(0, 0, 0, 0.2)"
          height={20}
          width={48}
          className="react-switch"
          id="material-switch"
        />
      </div>
    )
  );
}

export default TemperatureDisplay;
