import React from "react";

import FavoriteBorderIcon from "@material-ui/icons/FavoriteBorder";
import FavoriteIcon from "@material-ui/icons/Favorite";
import {
  addCityToFavorite,
  removeItemFromFavorites,
} from "../../../../store/weather";
import { Button } from "@material-ui/core";
import classes from "../weather-details.module.scss";
import { useDispatch } from "react-redux";

function FavoriteAction({ isItemInFavorite, currentCityKey }) {
  const dispatch = useDispatch();

  const onAddItemToFavorite = (_) => {
    dispatch(addCityToFavorite());
  };

  const onRemoveItemToFavorite = (e) => {
    e.stopPropagation();
    dispatch(removeItemFromFavorites(currentCityKey));
  };

  return (
    <div className={classes["favorite-action"]}>
      {isItemInFavorite ? <FavoriteIcon /> : <FavoriteBorderIcon />}
      <Button
        onClick={
          isItemInFavorite ? onRemoveItemToFavorite : onAddItemToFavorite
        }
        variant="contained"
        color="primary"
        size="small"
        className="ml-3">
        {isItemInFavorite ? "Remove from favourite" : "Add to Favorite"}
      </Button>
    </div>
  );
}

export default FavoriteAction;
