import React from "react";
import CustomCard from "../../Components/CustomCard/CustomCard";
import TemperatureDisplay from "../../Components/TemperatureDisplay/TemperatureDisplay";
import generateBgClassFromTemperature from "../../utils/generateBgClassFromTemperature";
import FavoriteAction from "../Weather/WeatherDetail/FavACtion/FavoriteAction";
import classes from "./favorites.module.scss";

function FavoriteItem({ weatherData, onFavoriteItemClick }) {
  const { currentWeatherInfo } = weatherData;

  const bgFromTemp = generateBgClassFromTemperature(
    currentWeatherInfo.Temperature.Metric.Value
  );

  const makeContent = (_) => (
    <section className={classes["item-header"]}>
      <img
        src={`/weatherIcons/${currentWeatherInfo.WeatherIcon}.png`}
        alt="current weather text"
      />

      <p className="is-size-4 is-size-6-mobile">
        {currentWeatherInfo.WeatherText}
      </p>
    </section>
  );

  const makeFooter = (_) => (
    <section>
      <div className="is-size-6 has-text-centered"> Current temperature</div>
      <TemperatureDisplay
        Temperature={currentWeatherInfo.Temperature.Metric}
      />{" "}
      <FavoriteAction isItemInFavorite currentCityKey={weatherData.Key} />
    </section>
  );

  return (
    <CustomCard
      customCardClass={`${classes.item} fav-item`}
      customStyle={{ transitionProperty: "all" }}
      onCardClick={(e) => onFavoriteItemClick(e, weatherData)}
      bgClass={bgFromTemp}
      header={weatherData.cityName}
      cardContent={makeContent(weatherData)}
      cardFooter={makeFooter(weatherData)}
    />
  );
}

export default FavoriteItem;
